<template>
	<div id="merchant-settle">
		<public-header title="入驻列表"></public-header>
		<div class="content">
			<div class="wrap-search wrap-search-input">
				<div class="tab-group flex-lr border-1px-b">
					<div class="tab-item" :class="{active: filter.items.status == 0}" @click="onToggleStatus(0)">全部</div>
					<div class="tab-item" :class="{active: filter.items.status == 2}" @click="onToggleStatus(2)">待审核</div>
					<div class="tab-item" :class="{active: filter.items.status == 1}" @click="onToggleStatus(1)">审核通过</div>
					<div class="tab-item" :class="{active: filter.items.status == 3}" @click="onToggleStatus(3)">审核未通过</div>
				</div>
				<van-search placeholder="请输入商户名称" v-model="filter.items.keyword" />
			</div>
			<van-pull-refresh v-model="isRefresh" @refresh="onPullDownRefresh()">
			<van-list
					v-model="records.loading"
					:finished="records.finished"
					@load="onLoad"
					:offset="100"
					:immediate-check="false"
					v-if="!records.empty"
					class="settle-list"
			>
				<div class="settle-item" v-for="(item, index) in records.data" :key="item.id">
					<div class="padding-10">
						<div class="flex-lr padding-10-b">
							<div class="font-bold">{{item.title}}</div>
							<div class="itag itag-primary" v-if="item.status == 1">审核通过</div>
							<div class="itag itag-danger" v-else-if="item.status == 2">待审核</div>
							<div class="itag itag-disabled" v-else-if="item.status == 3">审核未通过</div>
							<div class="itag" v-else-if="item.status == 4">回收站</div>
						</div>
						<div class="settle-info flex">
							<div>申请人：{{item.user.title}}</div>
						</div>
						<div class="settle-info flex">
							<div>联系电话：{{item.user.mobile}}</div>
						</div>
						<div class="settle-info flex">
							<div>商户地址：{{item.address}}</div>
						</div>
						<div class="settle-info flex">
							<div>申请时间：{{item.addtime_cn}}</div>
						</div>
					</div>
					<div class="border-1px-t btn-group">
						<div class="btn-item bg-info" v-if="item.status == 3 || item.status == 4" @click="onChangeStatus(item, 1, index)">审核通过</div>
						<div class="btn-item bg-danger" v-if="item.status == 1 || item.status == 4" @click="onChangeStatus(item, 3, index)">审核未通过</div>
						<router-link tag="div" class="btn-item bg-primary" :to="util.getUrl({path: '/pages/merchant/store/index', query:{id: item.id}})">编辑</router-link>
					</div>
				</div>
				<div class="loaded" v-if="records.finished">
					<div class="loaded-tips">没有更多了</div>
				</div>
			</van-list>
			<div class="no-data" v-else>
				<img src="static/img/order_no_con.png" alt="">
				<p>没有符合条件的数据!</p>
			</div>
			</van-pull-refresh>
		</div>
        <iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getRecords} from "@/controller/funcCommon"
	export default {
		data() {
			return {
				records:{
					page: 1,
					psize: 15,
					loading: false,
					finished: false,
					empty: false,
					data:[]
				},
				isRefresh: false,
				showPreLoading: true,
				filter: {
					items: {
						status: '0'
					}
				}
			}
		},
		components: {
			publicHeader,
		},
		methods: {
			onLoad(force){
				getRecords({
					vue: this,
					force: force,
					url: 'plateform/merchant/settle/list'
				});
			},
			onPullDownRefresh(){
				this.onLoad(true);
			},
			onToggleStatus(value){
				value = parseInt(value);
				if(this.filter.items.status == value) {
					return;
				}
				this.filter.items.status = value;
			},
			onChangeStatus(item, status, index){
				var that = this;
				that.util.jspost({
					url: 'plateform/merchant/settle/audit',
					data: {
						id: item.id,
						status: status
					},
					confirm: '确定修改审核状态吗？',
					success:function(){
						that.records.data[index].status = status;
					}
				});
			}
		},
		mounted(){
			this.onLoad();
		},
		watch: {
			filter: {
				handler(newValue, oldValue) {
					this.onLoad(true)
				},
				deep: true
			},
		}
	}
</script>

<style>
	#merchant-settle .content .van-search{
		padding: 10px;
	}
	#merchant-settle .content .settle-item{
		background-color: #fff;
		margin-bottom: 10px;
	}
	#merchant-settle .content .settle-item .settle-info{
		font-size: 14px;
		color: #7a7a7a;
		padding-bottom: 8px;
	}
	#merchant-settle .content .settle-item .settle-info:last-child{
		padding-bottom: 0px;
	}
	#merchant-settle .content .settle-item .btn-group{
		display: flex;
		flex-wrap: nowrap;
		justify-content: flex-end;
		align-items: center;
		width: 100%;
		padding: 10px;
	}
	#merchant-settle .content .settle-item .btn-group .btn-item{
		min-width: 60px;
		margin-left: 10px;
		text-align: center;
		padding: 10px 8px;
		border: 1px solid #e5e5e5;
		border-radius: 3px;
		font-size: 14px;
	}
</style>


